import React from 'react';

import classes from './BurgerIngredient.css';

const ingredient = ({type, remove}) => {
  console.log(type);
  let ig;
  switch(type) {
    case 'Bread-Top': {
     ig = (<div className={classes.BreadTop} onClick={remove}>
            <div className={classes.Seed1}></div>
            <div className={classes.Seed1}></div>
            <div className={classes.Seed2}></div>
            <div className={classes.Seed1}></div>
            <div className={classes.Seed2}></div>
            <div className={classes.Seed2}></div>
          </div>)
    }
    break;
    case 'Bread-Bottom': {
      ig = <div className={classes.BreadBottom} onClick={remove}></div>
    }
    break;
    case 'Meat': {
      ig = <div className={classes.Meat} onClick={remove}></div>
    } 
    break;
    case 'Cheese': {
      ig = <div className={classes.Cheese} onClick={remove}></div>
    }
    break;
    case 'Salad': {
      ig = <div className={classes.Salad} onClick={remove}></div>
    }
    break;
    case 'Bacon': {
      ig = <div className={classes.Bacon} onClick={remove}></div>
    }
    break;
    case 'Beef': {
      ig = <div className={classes.Beef} onClick={remove}></div>
    }
    break;
    case 'ShrededChicken': {
      ig = <div className={classes.ShrededChicken} onClick={remove}></div>
    }
    break;
    default: {
      ig = null;
    }
  } 

  return ig;
}

export default ingredient;