import React, { Component } from 'react';

 import Aux from '../../Components2/hoc/Auxi';

import Burger from '../../Components2/Burger/Burger';
import Plate from '../../Components2/Plate/Plate';
import BuildControls from '../../Components2/Burger/BuildControls/BuildControls';
import Modal from '../../Components2/UI/Modal/Modal';
import OrderSummary from '../../Components2/OrderSummary/OrderSummary';

const ingredientsPrice = {
  Salad: 1.3,
  Cheese: 2.2,
  Bacon: 3,
  Meat: 2.5,
  ShrededChicken: 1.1,
  Beef: 0.9
};

class BurgerBuilder extends Component {
  state = {
    ingredients: {
      Salad: 0,
      Cheese: 0,
      Bacon: 0,
      Meat: 0,
      ShrededChicken: 0,
      Beef: 0
    },
    ingredientsOrder: [],
    totalPrice: 10,
    showSummary: false
  }

  addIngredientHandler = (type) => {
    let { ingredients,ingredientsOrder,totalPrice } = this.state;

    ingredients[type]++;

    ingredientsOrder.push(type);

    totalPrice += ingredientsPrice[type];

    this.setState({ingredients, ingredientsOrder, totalPrice});
  }

  removeIngredientHandler = (type, i) => {
    let { ingredients, ingredientsOrder, totalPrice } = this.state;
    if (ingredients[type] > 0) {

      ingredients[type]--;

      ingredientsOrder.splice(i || ingredientsOrder.lastIndexOf(type), 1);

      totalPrice -= ingredientsPrice[type];

      this.setState({ingredients, ingredientsOrder, totalPrice});
    }
  }

  continuePurchaseHandler = () => {
    alert('Continued to Checkout');
  }

  cancelPurchaseHandler = () => {
    this.setState({showSummary: false});
  }

  purchaseHandler = () => {
    this.setState({showSummary: true});
  }

  render() {
    return (
      <Aux>
        <Modal show={this.state.showSummary} closeModal={this.cancelPurchaseHandler}>
          <OrderSummary 
            ingredients={this.state.ingredients}
            totalPrice={this.state.totalPrice}
            cancelPurchase={this.cancelPurchaseHandler}
            continuePurchase={this.continuePurchaseHandler} />
        </Modal>
        <Burger 
           ingredientsOrder={this.state.ingredientsOrder}
           removeIngredient={this.removeIngredientHandler} />
        <Plate />
        <BuildControls 
            showPurchase={this.purchaseHandler}
            price={this.state.totalPrice}
            ingredients={this.state.ingredients}
            addIngredient={this.addIngredientHandler}
            removeIngredient={this.removeIngredientHandler} />
      </Aux>
    );
  }
}

export default BurgerBuilder;