import React from 'react';

import clasess from './SideDrawer.css';
import Backdrop from '../../../components/UI/Backdrop/Backdrop';
import NavigationItems from '../../../components/Navigation/NavigationItems/NavigationItems';
import Logo from '../../Logo/Logo';
import Aux from '../../hoc/Auxi';

const sideDrawer = ({showDrawer, closeSideDrawer}) => (
  <Aux>
    <Backdrop show={showDrawer} clicked={closeSideDrawer}/>
    <div
     style={{transform: showDrawer ? 'translateX(0)' : 'translateX(-100vw)'}}
     className={clasess.SideDrawer}>
      <div className={clasess.Logo}>
        <Logo />
      </div>
      <nav>
        <NavigationItems />
      </nav>
    </div>
  </Aux>
);

export default sideDrawer;