import React from 'react';

import classes from './BuildControl2.css';
// import btnClass from '../../../UI/Button/Button';

const buildControl = (props) => (
  <div className={classes.BuildControl}>
    <div className={classes.Label}>{props.label}</div>
    <div className={classes.btns_wrapper}>
    <button
          onClick={props.removeIngredient}
          className={[classes.Less, classes.ctrl_btn].join(" ")}
          disabled={props.showBtn}>-</button>
      <button
          className={[classes.More, classes.ctrl_btn].join(" ")}
          onClick={props.addIngredient}>+</button>
    </div>
  </div>
);

export default buildControl;