import React from 'react';

import classes from './Toolbar.css';
import NavigationItems from '../NavigationItems/NavigationItems';
import MenuButton from '../Navigation/SideDrawer/Menu/MenuButton';

const listItems = [
  {href:'/', label: 'Order'},
  {href:'/', label: 'Checkout'}
];

const toolbar = (props) => {
  const { toggleSideDrawer } = props;

  return (
    <div className={classes.Toolbar}>
      <MenuButton clicked={toggleSideDrawer}/>
      <nav>
        <NavigationItems itemsData={listItems}/>
      </nav>
    </div>
  );
}

export default toolbar;
