import React from 'react';

import Aux from '../hoc/Auxi';
import Button from '../UI/Button/Button';

// so when rendering compoent the component might either
// use some logic or it wont whe the component doenst use any
// logic then it is a purely a functional component

const orderSummary = (props) => {
  const { 
    totalPrice,
    ingredients,
    cancelPurchase,
    continuePurchase} = props;

    console.log(cancelPurchase);
  return (
    <Aux>
      <h3>Your Order</h3>
      <p>Here is your delicous burger with this ingredients:</p>
      {
        Object.keys(ingredients).map((igKey, i) => {
          if (ingredients[igKey] > 0)
            return <p key={igKey+i}>{igKey}: {ingredients[igKey]}</p>
        })
      }
      <p>total Price: ${totalPrice.toFixed(2)}</p>
      <p>Press Continue to checkout?</p>
      <Button btnType="Danger" clicked={cancelPurchase}>Cancel</Button>
      <Button btnType="Success" clicked={continuePurchase}>Continue</Button>
    </Aux>
  );
}

export default orderSummary;