import React, { Component } from 'react';

import BurgerIngredient from './BurgerIngredient/BurgerIngredient';
import classes from './Burger.css';

class Burger extends Component {
  render() {
    const { ingredientsOrder, removeIngredient } = this.props;
    let burgerIngredients = ingredientsOrder
    .map((ig, i) => (
      <BurgerIngredient key={ig+i} type={ig}
       remove={() => removeIngredient(ig, i)} />
    ));

    if (burgerIngredients.length === 0) {
      burgerIngredients = <p><strong>Please Insert Some Ingredients</strong></p>
    }
  
    console.log(burgerIngredients);
  
    return (
      <div className={classes.Burger}>
        <BurgerIngredient type="Bread-Top"/>  
        { burgerIngredients }
        <BurgerIngredient type="Bread-Bottom" />
      </div>
    );
  }
} 

export default Burger;