import React from 'react';

import classes from './BuildControls.css';
import BuildControl from './BuildControl2/BuildControl2';
import Button from '../../UI/Button/Button';

const buildControls = (props) => {
  const {
    price,
    ingredients,
    addIngredient,
    removeIngredient,
    showPurchase} = props;

  let ctrls = Object.keys(ingredients).map((igKey, i) => (
    <BuildControl 
       key={igKey+i} 
       label={igKey}
       addIngredient={() => addIngredient(igKey)}
       removeIngredient={() => removeIngredient(igKey)}
       showBtn={ingredients[igKey] == 0} />
  ));

  return (
    <div className={classes.container}>
      <p className={classes.price}><strong>price: </strong>{price.toFixed(2)}</p>
      <div className={classes.BuildControls}>
        {ctrls}
      </div>
      <Button 
        clicked={showPurchase}
        btnType="Success">ORDER NOW</Button>
    </div>
  );
};

export default buildControls;